@import './vars';

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.footer {
  margin-top: auto;
}

.sm-only {
  display: none;
  @media screen and (max-width: $screen-sm - 1px) {
    display: block;
  }
}

.page-wrapper {
  padding: 20px 0 80px 0;
  .container {
    display: flex;
    flex-wrap: wrap;
  }
  &__main {
    width: calc(100% - #{$sidebar-width});
    padding-right: 15px;
  }
  &__aside {
    width: $sidebar-width;
    padding-left: 15px;
  }
}

@media screen and (max-width: $screen-xl - 1px) {
  .page-wrapper {
    &__main {
      width: 60%;
    }
    &__aside {
      width: 40%;
    }
  }
}

@media screen and (max-width: $screen-md - 1px) {
  .page-wrapper {
    &__main {
      width: 100%;
      padding-right: 0;
      margin-bottom: 30px;
    }
    &__aside {
      width: 100%;
      padding-left: 0;
    }
  }
}

.disable-scroll {
  overflow: hidden;
  padding-right: 15px;
  @media screen and (max-width: $screen-md - 1px) {
    padding-right: 0px;
  }
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.section-title {
  &__sub {
    display: block;
    font-size: 18px;
    text-transform: uppercase;
    color: $color-secondary;
  }
  &__main {
    font-size: 50px;
    b {
      font-weight: 700;
    }
  }
}

@media screen and (max-width: $screen-sm - 1px) {
  .section-title {
    &__sub {
      font-size: 16px;
    }
    &__main {
      font-size: 32px;
    }
  }
}

.link {
  @include link-hover();
  text-transform: uppercase;
  font-size: 14px;
  color: $color-secondary;
  font-weight: 500;
  &:hover {
    color: $color-primary;
  }
  &--arrow {
    svg {
      transition: transform 0.3s;
      margin-left: 5px;
    }
    &:hover {
      svg {
        transform: translateX(3px);
      }
    }
  }
}
